import { instance, APIResponceType, ResultCodeForCaptcha, ResultCodesEnum } from './api';



type LoginRespoceType = {
    userId: number
    token?: string
}

type MyResponceType = {
    id: number
    email: string
    login: string
}



export const authAPI = {
    me() {
        return instance
            .get<APIResponceType<MyResponceType>>(`auth/me`).then(res => res.data);
    },
    async login(email: string, password: string, rememberMe: boolean = false, captcha: string | null = null) {
        const response = await instance.post<APIResponceType<LoginRespoceType, ResultCodeForCaptcha | ResultCodesEnum>>(
            `auth/login`,
            { email, password, rememberMe, captcha }
        );

        //    localStorage,     
        if (response.data.resultCode === ResultCodesEnum.Success && response.data.data.token) {
            localStorage.setItem("sn-token", response.data.data.token);
        }

        return response.data;
    },

    async loginWithGoogle(googleToken: string | undefined) {
        const response = await instance.post<APIResponceType<LoginRespoceType>>(
            `auth/login`,
            { token: googleToken }
        );

        if (response.data.resultCode === ResultCodesEnum.Success && response.data.data.token) {
            localStorage.setItem("sn-token", response.data.data.token);
        }

        return response.data;
    },

    logout() {
        //    localStorage  
        localStorage.removeItem("sn-token");
        return instance
            .delete(`auth/login`).then(res => res.data);
    },
};